import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import UnderlineText from "../../../components/UnderlineText/UnderlineText";
import DeriNumberFormat from "../../../utils/DeriNumberFormat";
import { inject, observer } from 'mobx-react';
import { useLocation } from 'react-router-dom'
import Type from '../../../model/Type';
import Category from '../../../model/Category'
import Intl from '../../../model/Intl'
import classNames from 'classnames';
import './tradeInfo.less'
import Icon from '../../../components/Icon/Icon';
import Input from '../../../components/Input/Input';
import Tooltip from '../../../components/Tooltip/Tooltip';
import ApiProxy from '../../../model/ApiProxy';
import { bg } from '../../../lib/web3js';
import { show } from 'react-functional-modal'
import SimulatePnl from '../../../components/SimulatePnl/SimulatePnl';
import { isMobile } from '../../../utils/utils';
const getLang = key => {
  return Intl.get('trade', key)
}

const PowerDailyFundingDetail = ({ wallet, lang, trading }) => {
  const [calculatePnl, setCalculatePnl] = useState('')
  const [value, setValue] = useState(50)
  const [isExpand, setIsExpand] = useState(false)
  const pnlCalculate = async (rate) => {
    if (rate) {
      if (bg(rate).lt(-100)) {
        rate = '-100'
      }
      setValue(rate)
      const afterDiv = bg(rate).div(100).toFixed(2)
      const params = [wallet.detail.chainId, trading.symbolInfo.address, trading.symbolInfo.symbol, afterDiv]
      const pnl = await ApiProxy.request('getSimulatePnl', params)
      setCalculatePnl(bg(pnl).times(100).toFixed(0))
    } else {
      setCalculatePnl('')
      setValue('')
    }
  }
  const expand = () => {
    setIsExpand(!isExpand)
  }
  const arrowClazz = classNames('pdfd', {
    show: isExpand
  })
  useEffect(() => {
    if (wallet.detail && trading.symbolInfo && trading.symbolInfo.symbol) {
      pnlCalculate(value)
    }
  }, [wallet.detail, trading.symbolInfo])
  return (
    <div className='right-side'>
      <Icon token={isExpand ? 'arrow-left' : 'arrow-right'} width='20' onClick={expand} className='power-funding-desc' data-for='pdfd-tooltip' data-event='click' data-tip='tooltip' />
      <div className={arrowClazz}>
        <div className='pdfd-right'>
          <div className='label'>{lang['simulate-pnl']}</div>
          <div className='simulate light'>
            {trading.symbolInfo.symbolWithoutPower}
            <Input className='before simulate-input' onChange={pnlCalculate} unit='%' inputWdith='48px' value={value} disableNegtive={false} inputHeight='20px' />
            {`->  ${trading.symbolInfo && trading.symbolInfo.displaySymbol && trading.symbolInfo.displaySymbol}`}
            <div className={classNames({ up: bg(calculatePnl).isPositive(), down: bg(calculatePnl).isNegative() })}>
              {calculatePnl && calculatePnl + '%'}
            </div>
            {/* <Input className={classNames('simulate-input',{up : bg(calculatePnl).isPositive() , down : bg(calculatePnl).isNegative()})} unit='%' inputWdith='64px' inputHeight='20px' value={calculatePnl}/> */}
          </div>
        </div>
      </div>
    </div>
  )
}

function TradeInfo({ trading, lang, wallet }) {
  const [columns, setColumns] = useState([])
  const location = useLocation();
  let isRetired = location.pathname.split('/')[1] === 'retired' ? true : false
  const fileds = {
    'mark-price': { text: getLang('mark-price'), value: '', decimal: trading.symbolInfo.decimals, type: 'number' },
    'index-price': { text: trading.symbolInfo.isPower ? `${trading.symbolInfo.symbolWithoutPower} ${getLang('price')}` : getLang('index-price'), value: '', decimal: 2, type: 'number' },
    'dvol': { text: getLang('dvol'), tip: getLang('dvol-hover'), value: '', suffix: '%', decimal: 2 },
    'daily-funding': { text: getLang('daily-funding'), value: '', suffix: '%', decimal: 2 },
    '24h-change': { text: getLang('24h-change'), value: '', type: 'number', suffix: '%' },
    'total-net-position': { text: getLang('total-net-position'), tip: getLang('total-net-position-hover'), value: '', decimal: 2, type: 'number' },
    'margin-req': { text: getLang('margin-req'), value: '', tip: getLang('margin-req-hover') },
    'fee': { text: getLang('fee'), value: '' },
    "payoff": { text: getLang('payoff'), value: '', tip: getLang('payoff-hover') },
    'up-depth': { text: getLang('up-depth'), value: '', tip: getLang('up-depth-hover') },
    "power-price": { text: trading.symbolInfo.symbolWithoutPower && trading.symbolInfo.symbolWithoutPower + getLang('power-price'), value: '', decimal: trading.symbolInfo.decimals }
  }
  const showSimulatePnl = (wallet, trading, lang) => {
    show(<SimulatePnl wallet={wallet} lang={lang} trading={trading} />, {
      key: 'simulatePnl',
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)",
        zIndex: 2,
      }
    })
  }
  useEffect(() => {
    if (trading.fundingRate) {
      let cols = []
      if (Type.isFuture) {
        cols = [
          { ...fileds['mark-price'], value: trading.markPrice },
          { ...fileds['index-price'], value: trading.index },
          {
            ...fileds['daily-funding'], value: () => {
              return (
                <>
                  <UnderlineText tip={trading.fundingFeeValueTip} multiline className='funding-rate'>
                    <DeriNumberFormat value={`${trading.fundingRate.funding0}`} decimalScale='4' />
                  </UnderlineText>
                  &nbsp;&nbsp;
                  <UnderlineText tip={trading.rateTip} multiline className='funding-rate'>
                    <DeriNumberFormat prefix='(' suffix='%)' decimalScale='4' value={trading.fundingFeeRate} />
                  </UnderlineText>
                </>)
            }
            , tip: trading.fundingFeeTip
          },
          { ...fileds['24h-change'], value: trading.symbolInfo.volumn24h * 100, className: trading.symbolInfo.volumn24h ? (trading.symbolInfo.volumn24h > 0 ? 'up calculator' : 'down calculator') : 'calculator', icon: isRetired ? '' : <Icon token='calculator' onClick={() => showSimulatePnl(wallet, trading, lang)} />, iconTip: isRetired ? '' : lang['simulate-pnl-icon-tip'] },
          { ...fileds['total-net-position'], value: () => <DeriNumberFormat value={trading.fundingRate.tradersNetVolume} suffix={` ${trading.symbolInfo.unit}`} /> },
          { ...fileds['up-depth'], value: () => <DeriNumberFormat decimalScale='2' value={trading.fundingRate.impactUpNotional} suffix={` ${trading.symbolInfo.unit}`} /> }
        ]
        if (trading.symbolInfo.isV2) {
          cols.pop()
        }
        if (Category.isPower) {
          cols = [
            { ...fileds['mark-price'], value: trading.markPrice, tip: trading.powerMarkPriceTip },
            { ...fileds['index-price'], value: () => <><DeriNumberFormat value={trading.index} decimalScale={2} /> | DVOL : <DeriNumberFormat value={trading.volatility} decimalScale={2} suffix='%' /></> },
            { ...fileds['power-price'], value: trading.fundingRate.powerPrice },
            {
              ...fileds['daily-funding'], value: () => {
                return (
                  <div>
                    <UnderlineText tip={trading.fundingFeeValueTip} multiline className='funding-rate'>
                      <DeriNumberFormat value={`${trading.fundingFee}`} decimalScale='2' />
                    </UnderlineText>
                    &nbsp;&nbsp;
                    <UnderlineText tip={trading.rateTip} multiline className='funding-rate'>
                      <DeriNumberFormat prefix='(' suffix='%)' decimalScale='4' value={trading.fundingFeeRate} />
                    </UnderlineText>
                  </div>)
              }, tip: trading.fundingFeeTip
            },
            {
              ...fileds['24h-change'], value: () => <> <span className={trading.symbolInfo.volumn24h && (trading.symbolInfo.volumn24h > 0 ? 'up' : 'down')}> <DeriNumberFormat value={trading.symbolInfo.volumn24h * 100} decimalScale='2' suffix="%" /></span> | {trading.symbolInfo.symbolWithoutPower}: <span className={trading.symbolInfo.indexPriceVolumn24h && (trading.symbolInfo.indexPriceVolumn24h > 0 ? 'up' : 'down')}><DeriNumberFormat value={trading.symbolInfo.indexPriceVolumn24h * 100} suffix="%" decimalScale='2' /></span>  </>
              // , rightSide: <PowerDailyFundingDetail wallet={wallet} trading={trading} lang={lang} />
              , icon: <Icon token='calculator' onClick={() => showSimulatePnl(wallet, trading, lang)} />, className: 'calculator', iconTip: lang['simulate-pnl-icon-tip']
            },
            // { ...fileds['24h-change'], value: trading.symbolInfo.volumn24h * 100, className: trading.symbolInfo.volumn24h && (trading.symbolInfo.volumn24h > 0 ? 'up' : 'down') },
            { ...fileds['total-net-position'], value: () => <DeriNumberFormat value={trading.fundingRate.tradersNetVolume} suffix={` ${trading.symbolInfo.unit}`} /> },
            { ...fileds['up-depth'], value: () => <DeriNumberFormat decimalScale='2' value={trading.fundingRate.impactUpNotional} suffix={` ${trading.symbolInfo.unit}`} /> }
          ]
        }
      } else {
        cols = [
          { ...fileds['mark-price'], value: trading.markPrice },
          { ...fileds['dvol'], text: trading.symbolInfo.underlier, value: () => <><DeriNumberFormat value={trading.index} decimalScale={2} /> | DVOL : <DeriNumberFormat value={trading.volatility} decimalScale={2} suffix='%' /></> },
          { ...fileds['payoff'], value: () => <DeriNumberFormat value={trading.fundingRate.payoff} /> },
          {
            ...fileds['daily-funding'], value: () => {
              return (
                <UnderlineText tip={trading.fundingFeeValueTip} multiline><DeriNumberFormat value={trading.fundingFee} decimalScale='4' />
                </UnderlineText>
              )
            }, tip: trading.fundingFeeTip
          },
          { ...fileds['24h-change'], value: trading.symbolInfo.volumn24h * 100, className: trading.symbolInfo.volumn24h ? (trading.symbolInfo.volumn24h > 0 ? 'up calculator' : 'down calculator') : 'calculator', icon: isRetired ? '' : <Icon token='calculator' onClick={() => showSimulatePnl(wallet, trading, lang)} />, iconTip: isRetired ? '' : lang['simulate-pnl-icon-tip'] },
          { ...fileds['total-net-position'], value: () => <DeriNumberFormat value={trading.fundingRate.tradersNetVolume} suffix={` ${trading.symbolInfo && trading.symbolInfo.unit}`} /> },
          { ...fileds['margin-req'], value: () => <><DeriNumberFormat value={trading.contract.initialMarginRatio * 100} suffix='%' decimalScale='2' /> | <DeriNumberFormat value={trading.contract.maintenanceMarginRatio * 100} suffix='%' decimalScale='2' /></> },
          { ...fileds['fee'], value: () => <>{trading.feeDisplay[0]} * <DeriNumberFormat value={trading.feeDisplay[2]} suffix='%' /></> },
          { ...fileds['up-depth'], value: () => <DeriNumberFormat decimalScale='2' value={trading.fundingRate.impactUpNotional} suffix={` ${trading.symbolInfo.unit ? trading.symbolInfo.unit : ''}`} /> },
        ]
      }
      setColumns(cols)
    }
  }, [trading.fundingRate, trading.volatility, trading.fee, trading.marginReqs, trading.dailyFunding, trading.symbolInfo.markPrice, trading.markPrice, trading.index])
  return (
    <div className='trade-info-content'>
      <div className='slide-icon left'><Icon token='arrow-left' width='24' height='24' /></div>
      <div className='m-body'>
        {columns.map((col, index) => (
          <div className='m-col' key={index}>
            <div className='main'>
              <div className='m-col-name'>
                {col.tip ? <UnderlineText id={col.id} text={col.text} tip={col.tip} width={col.tipWidth} multiline /> : col.text}
              </div>
              <div className={classNames('col-value', col.className)}>
                {typeof col.value === 'function'
                  ? col.value(col)
                  : <DeriNumberFormat value={col.value} prefix={col.prefix} suffix={col.suffix} decimalScale={col.decimal || 2} />}
                {col.icon && <>
                  {col.iconTip && !isMobile() ? <UnderlineText tip={col.iconTip}>{col.icon}</UnderlineText> : col.icon}
                </>}
              </div>
            </div>
            {col.rightSide}
          </div>
        ))}
      </div>
      <div className='slide-icon right' ><Icon token='arrow-right' width='24' height='24' /></div>
    </div>
  )
}
export default inject('trading', 'wallet')(observer(TradeInfo))